.Sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  min-height: 100vh;
  width: 320px;
  align-self: flex-start;
  display: flex;
  flex-direction: column;
  padding-bottom: 24px;
  background-color: #fff;
  will-change: width;
  transition: width 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 10px;
  z-index: 10;
}

.Sidebar-closed {
  width: 72px;
}

.Sidebar-decor {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding-right: 20px;
  background-color: #161d2a;
  color: #eee;
  overflow: hidden;
}

.toggle-btn {
  position: absolute;
  right: -16px;
  top: 96px;
  height: 32px;
  width: 32px;
  background: var(--accent-color) url(/react-university-03-state-forms/static/media/hide.ed0b8ac3.svg) 49% 50% no-repeat;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: inset -4px -4px 7px rgba(0, 0, 0, 0.2);
  transition: all 300ms;
}

.toggle-btn:hover {
  -webkit-animation-name: goLeft;
          animation-name: goLeft;
  -webkit-animation-duration: 1.2s;
          animation-duration: 1.2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes goLeft {
  0% {
    background-position: 50% 50%;
  }
  50% {
    background-position: -60% 50%;
  }
  51% {
    background-position: 160% 50%;
  }
  100% {
    background-position: 50% 50%;
  }
}

@keyframes goLeft {
  0% {
    background-position: 50% 50%;
  }
  50% {
    background-position: -60% 50%;
  }
  51% {
    background-position: 160% 50%;
  }
  100% {
    background-position: 50% 50%;
  }
}

